.Container {
    $transition: all 0.2s ease-in-out;
    position: relative;
    width: 400px;
    padding: 15px 0;
    margin: 0 auto;

    input {
        width: 100%;
        height: 38px;
        background: #fff;
        font-size: 1.1rem;
        font-weight: bold;
        text-transform: uppercase;
        padding: 4px 8px;
        border: 0;
        border: {
            bottom: 2px solid #202020;
            radius: 0;
        }
        box-shadow: none;
        box-sizing: border-box;
        transition: $transition;

        &:focus {
            outline: none;
            border-bottom: 2px solid #f0141e;
            transition: $transition;

            ~ button { color: #f0141e; }
        }

        /* Chrome, Firefox, Opera, Safari 10.1+ */
        &::placeholder { 
            color: #ccc;
            opacity: 1;
        }
          
        /* Internet Explorer 10-11 */
        &:-ms-input-placeholder { color: #ccc; }
          
        /* Microsoft Edge */
        &::-ms-input-placeholder { color: #ccc; }
    }

    button, button:focus {
        position: absolute;
        right: 0;
        top: 18px;
        background: transparent;
        color: #000;
        font-size: 1.25rem;
        border: 0;
        box-shadow: none;
        padding: 5px 10px;
        outline: none;
        box-shadow: none;
        cursor: pointer;
        transition: $transition;
    }
    button:hover {
        color: #f0141e;
        transition: $transition;
    }
}